@use '../helpers' as h;

.c-btn {
}

.button {
    padding: 30px 50px;
    text-align: center;
    background: h.$accent-color;
    background:  linear-gradient(-45deg, transparent 15px, h.$accent-color 0);
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    cursor: pointer;
    transition: all ease 0.3s;
    &:hover{
        background:  linear-gradient(-45deg, transparent 15px, #a10a0a 0);
    }
}

.button.button-small {
    padding: 22px 48px;
 
}

.button.fixed{
    width: 280px;
    margin: 0 auto ;
    display: block;

}
